import React, {Component} from 'react'

class Timer extends Component{

  constructor(){
    super()

    this.state = { 
      count : 0
    }
  }

  increment(){
    setInterval(()=>{
      this.setState({
        count : this.state.count + 1
      })
    },10)
  }

  render(){
    return(
      <div>
        <h1>{this.state.count}</h1>
        <button onClick={this.increment.bind(this)}>Click</button>
      </div>
    )
  }
}

export default Timer